<template>
  <div class="server-management-page">
    <!-- 页面头部 -->
    <div class="page-header">
      <div class="header-left">
        <h2>服务器管理</h2>
        <p class="page-description">管理系统服务器状态和配置</p>
      </div>
      <div class="header-actions">
        <el-button @click="refreshServerList" :loading="loading" type="primary">
          <el-icon><Refresh /></el-icon>
          刷新
        </el-button>
        <el-button @click="showAddDialog = true" type="success">
          <el-icon><Plus /></el-icon>
          添加服务器
        </el-button>
      </div>
    </div>

    <!-- 表头 -->
    <div class="table-header ba-scroll-style">
      <!-- 刷新、添加、编辑、删除按钮 -->
      <el-button :icon="Refresh" @click="refreshServerList" color="#40485b"/>
      <el-button type="primary" :icon="Plus" @click="showAddDialog = true">添加</el-button>
      <el-button type="primary" :disabled="!hasSelectedRows" @click="handleBatchEdit">编辑</el-button>
      <el-button type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete">删除</el-button>
      
      <!-- 部署按钮 -->
      <el-button type="success" :icon="Upload" @click="showDeployDialog = true">部署</el-button>
      
      <!-- 搜索按钮 -->
      <div class="table-search">
        <el-button-group class="ml-4">
          <el-button :icon="Search" @click="toggleSearch" />
        </el-button-group>
      </div>
    </div>

    <!-- 搜索卡片 -->
    <transition name="fade">
      <el-card v-show="showSearch" class="search-card">
        <template #header>
          <div class="search-header">
            <div class="search-title">
              <el-icon><Search /></el-icon>
              搜索条件
            </div>
            <el-button type="text" @click="toggleSearch" class="close-btn">
              <el-icon><Close /></el-icon>
            </el-button>
          </div>
        </template>
        <el-form :model="searchForm" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="服务器名称">
                <el-input v-model="searchForm.server_name" placeholder="请输入服务器名称" clearable />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="服务器IP">
                <el-input v-model="searchForm.server_ip" placeholder="请输入服务器IP" clearable />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="状态">
                <el-select v-model="searchForm.status" placeholder="请选择状态" clearable>
                  <el-option label="在线" value="online" />
                  <el-option label="离线" value="offline" />
                  <el-option label="维护中" value="maintenance" />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="维护状态">
                <el-select v-model="searchForm.is_maintenance" placeholder="请选择维护状态" clearable>
                  <el-option label="正常" :value="false" />
                  <el-option label="维护中" :value="true" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <div class="search-buttons">
            <el-button type="primary" @click="handleSearch">搜索</el-button>
            <el-button @click="resetSearch">重置</el-button>
          </div>
        </el-form>
      </el-card>
    </transition>

    <!-- 表格内容 -->
    <div style="width: 100%; overflow-x: auto;" class="table-content">
      <el-table 
        :data="serverList" 
        border 
        stripe 
        table-layout="fixed" 
        style="width: 100%;" 
        v-loading="loading"
        empty-text="暂无服务器数据"
        @selection-change="handleSelectionChange"
      >
        <!-- 选择列 -->
        <el-table-column type="selection" width="40" />
        
        <!-- ID列 -->
        <el-table-column prop="id" label="ID" align="center" min-width="80"/>
        
        <!-- 服务器信息 -->
        <el-table-column prop="server_name" label="服务器名称" align="center" min-width="150"/>
        <el-table-column prop="server_ip" label="服务器IP" align="center" min-width="120"/>
        
        <!-- 系统指标分组 -->
        <el-table-column label="系统指标" align="center" min-width="300">
          <el-table-column label="CPU使用率" width="100" align="center">
            <template #default="{ row }">
              <div class="metric-item">
                <el-progress 
                  :percentage="row.cpu_usage || 0" 
                  :color="getProgressColor(row.cpu_usage)"
                  :stroke-width="6"
                  :show-text="false"
                />
                <span class="metric-value">{{ row.cpu_usage || 0 }}%</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="内存使用率" width="100" align="center">
            <template #default="{ row }">
              <div class="metric-item">
                <el-progress 
                  :percentage="row.memory_usage || 0" 
                  :color="getProgressColor(row.memory_usage)"
                  :stroke-width="6"
                  :show-text="false"
                />
                <span class="metric-value">{{ row.memory_usage || 0 }}%</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="磁盘使用率" width="100" align="center">
            <template #default="{ row }">
              <div class="metric-item">
                <el-progress 
                  :percentage="row.disk_usage || 0" 
                  :color="getProgressColor(row.disk_usage)"
                  :stroke-width="6"
                  :show-text="false"
                />
                <span class="metric-value">{{ row.disk_usage || 0 }}%</span>
              </div>
            </template>
          </el-table-column>
        </el-table-column>

        <!-- 状态列 -->
        <el-table-column label="状态" align="center" min-width="100">
          <template #default="{ row }">
            <el-tag :type="getStatusType(row.status)" size="small">
              {{ getStatusText(row.status) }}
            </el-tag>
          </template>
        </el-table-column>

        <!-- 维护状态列 -->
        <el-table-column label="维护状态" align="center" min-width="100">
          <template #default="{ row }">
            <el-switch
              v-model="row.is_maintenance"
              @change="handleToggleMaintenance(row)"
              :loading="row.maintenanceLoading"
            />
          </template>
        </el-table-column>

        <!-- 备注列 -->
        <el-table-column prop="remark" label="备注" align="center" min-width="150" show-overflow-tooltip/>

        <!-- 创建时间列 -->
        <el-table-column prop="created_at" label="创建时间" align="center" min-width="160"/>

        <!-- 操作列 -->
        <el-table-column label="操作" align="center" fixed="right" min-width="200">
          <template #default="{ row }">
            <el-button 
              @click="openEditDialog(row)" 
              type="primary" 
              size="small"
            >
              编辑
            </el-button>
            <el-button 
              @click="viewServerDetail(row)" 
              type="info" 
              size="small"
            >
              详情
            </el-button>
            <el-button 
              v-if="row.status === 'offline'"
              @click="handleDeleteServer(row)" 
              type="danger" 
              size="small"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          v-model:current-page="pagination.current_page"
          v-model:page-size="pagination.per_page"
          :page-sizes="[10, 20, 50, 100]"
          :total="pagination.total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>

    <!-- 添加/编辑服务器对话框 -->
    <el-dialog
      v-model="showAddDialog"
      :title="editingServer ? '编辑服务器' : '添加服务器'"
      width="600px"
      @close="resetForm"
    >
      <el-form
        ref="serverFormRef"
        :model="serverForm"
        :rules="serverRules"
        label-width="100px"
      >
        <el-form-item label="服务器名称" prop="server_name">
          <el-input v-model="serverForm.server_name" placeholder="请输入服务器名称" />
        </el-form-item>
        
        <el-form-item label="服务器IP" prop="server_ip">
          <el-input v-model="serverForm.server_ip" placeholder="请输入服务器IP地址" />
        </el-form-item>
        
        <el-form-item label="服务器端口" prop="server_port">
          <el-input-number 
            v-model="serverForm.server_port" 
            :min="1" 
            :max="65535" 
            placeholder="端口号"
          />
        </el-form-item>
        
        <el-form-item label="维护状态">
          <el-switch
            v-model="serverForm.is_maintenance"
          />
        </el-form-item>
        
        <el-form-item label="备注" prop="remark">
          <el-input
            v-model="serverForm.remark"
            type="textarea"
            :rows="3"
            placeholder="请输入备注信息"
            maxlength="500"
            show-word-limit
          />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <el-button @click="showAddDialog = false">取消</el-button>
        <el-button 
          type="primary" 
          @click="saveServer"
          :loading="saveLoading"
        >
          {{ editingServer ? '更新' : '添加' }}
        </el-button>
      </template>
    </el-dialog>

    <!-- 服务器详情对话框 -->
    <el-dialog
      v-model="showDetailDialog"
      title="服务器详情"
      width="800px"
    >
      <div v-if="selectedServer" class="server-detail">
        <el-descriptions :column="2" border>
          <el-descriptions-item label="服务器名称">
            {{ selectedServer.server_name || '未命名' }}
          </el-descriptions-item>
          <el-descriptions-item label="服务器IP">
            {{ selectedServer.server_ip }}
          </el-descriptions-item>
          <el-descriptions-item label="服务器端口">
            {{ selectedServer.server_port || 'N/A' }}
          </el-descriptions-item>
          <el-descriptions-item label="状态">
            <el-tag :type="getStatusType(selectedServer.status)">
              {{ getStatusText(selectedServer.status) }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="维护状态">
            <el-switch
              v-model="selectedServer.is_maintenance"
              disabled
            />
          </el-descriptions-item>
          <el-descriptions-item label="最后更新">
            {{ formatTime(selectedServer.last_update) }}
          </el-descriptions-item>
          <el-descriptions-item label="备注" :span="2">
            {{ selectedServer.remark || '无备注' }}
          </el-descriptions-item>
        </el-descriptions>

        <!-- 系统指标图表 -->
        <div class="metrics-chart">
          <h4>系统指标</h4>
          <div class="chart-container">
            <div class="metric-chart">
              <div class="metric-label">CPU使用率</div>
              <el-progress 
                :percentage="selectedServer.cpu_usage || 0" 
                :color="getProgressColor(selectedServer.cpu_usage)"
                :stroke-width="20"
              />
            </div>
            <div class="metric-chart">
              <div class="metric-label">内存使用率</div>
              <el-progress 
                :percentage="selectedServer.memory_usage || 0" 
                :color="getProgressColor(selectedServer.memory_usage)"
                :stroke-width="20"
              />
            </div>
            <div class="metric-chart">
              <div class="metric-label">磁盘使用率</div>
              <el-progress 
                :percentage="selectedServer.disk_usage || 0" 
                :color="getProgressColor(selectedServer.disk_usage)"
                :stroke-width="20"
              />
            </div>
          </div>
        </div>
      </div>
    </el-dialog>

    <!-- 部署对话框 -->
    <el-dialog
      v-model="showDeployDialog"
      title="服务器部署"
      width="600px"
      :close-on-click-modal="false"
    >
      <div class="deploy-content">
        <el-form :model="deployForm" label-width="120px">
          <el-form-item label="部署模式">
            <el-radio-group v-model="deployForm.mode">
              <el-radio label="update_restart">更新并重启Webman</el-radio>
              <el-radio label="update_only">只更新无需重启</el-radio>
            </el-radio-group>
          </el-form-item>
          
          <el-form-item label="目标服务器">
            <el-select v-model="deployForm.serverIds" multiple placeholder="选择要部署的服务器" style="width: 100%">
              <el-option
                v-for="server in serverList"
                :key="server.id"
                :label="`${server.server_name} (${server.server_ip})`"
                :value="server.id"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item label="部署说明">
            <el-input
              v-model="deployForm.description"
              type="textarea"
              :rows="3"
              placeholder="请输入部署说明..."
            />
          </el-form-item>
        </el-form>
      </div>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="showDeployDialog = false">取消</el-button>
          <el-button type="primary" @click="handleDeploy" :loading="deployLoading">
            开始部署
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import {
  getServerList,
  addServer,
  updateServer,
  toggleMaintenance,
  deleteServer,
  deployServers
} from '@/api/server'
import { 
  Refresh, 
  Plus, 
  Edit, 
  Delete,
  View, 
  Monitor,
  Check,
  Warning,
  Close,
  Search,
  Upload
} from '@element-plus/icons-vue'

// 响应式数据
const loading = ref(false)
const saveLoading = ref(false)
const showAddDialog = ref(false)
const showDetailDialog = ref(false)
const showSearch = ref(false)
const showDeployDialog = ref(false)
const editingServer = ref(null)
const deployLoading = ref(false)
const selectedServer = ref(null)
const selectedRows = ref([])
const hasSelectedRows = ref(false)

const serverList = ref([])
const pagination = reactive({
  current_page: 1,
  per_page: 20,
  total: 0
})

const serverForm = reactive({
  server_name: '',
  server_ip: '',
  server_port: 80,
  is_maintenance: false,
  remark: ''
})

const deployForm = reactive({
  mode: 'update_restart',
  serverIds: [],
  description: ''
})

const searchForm = reactive({
  server_name: '',
  server_ip: '',
  status: '',
  is_maintenance: null
})

const serverRules = {
  server_name: [
    { required: true, message: '请输入服务器名称', trigger: 'blur' }
  ],
  server_ip: [
    { required: true, message: '请输入服务器IP', trigger: 'blur' },
    { pattern: /^(\d{1,3}\.){3}\d{1,3}$/, message: '请输入正确的IP地址', trigger: 'blur' }
  ]
}

// 获取服务器列表
const refreshServerList = async () => {
  loading.value = true
  try {
    const params = {
      page: pagination.current_page,
      per_page: pagination.per_page,
      ...searchForm
    }
    const response = await getServerList(params)
    if (response.code === 200) {
      serverList.value = response.data.data.map(server => ({
        ...server,
        maintenanceLoading: false
      }))
      pagination.total = response.data.total
    } else {
      ElMessage.error(`获取服务器列表失败: ${response.message}`)
    }
  } catch (error) {
    ElMessage.error('获取服务器列表失败')
    console.error('获取服务器列表失败:', error)
  } finally {
    loading.value = false
  }
}

// 切换维护状态
const handleToggleMaintenance = async (server) => {
  server.maintenanceLoading = true
  try {
    const response = await toggleMaintenance(server.id, server.is_maintenance)
    if (response.code === 200) {
      ElMessage.success(`服务器${server.is_maintenance ? '已开启' : '已关闭'}维护模式`)
    } else {
      ElMessage.error(`操作失败: ${response.message}`)
      server.is_maintenance = !server.is_maintenance // 回滚状态
    }
  } catch (error) {
    ElMessage.error('操作失败')
    server.is_maintenance = !server.is_maintenance // 回滚状态
  } finally {
    server.maintenanceLoading = false
  }
}

// 编辑服务器
const openEditDialog = (server) => {
  editingServer.value = server
  Object.assign(serverForm, {
    server_name: server.server_name,
    server_ip: server.server_ip,
    server_port: server.server_port,
    is_maintenance: server.is_maintenance,
    remark: server.remark
  })
  showAddDialog.value = true
}

// 查看服务器详情
const viewServerDetail = (server) => {
  selectedServer.value = server
  showDetailDialog.value = true
}

// 删除服务器
const handleDeleteServer = async (server) => {
  try {
    await ElMessageBox.confirm(
      `确定要删除服务器 "${server.server_name}" 吗？`,
      '确认删除',
      {
        type: 'warning',
        confirmButtonText: '确定删除',
        cancelButtonText: '取消'
      }
    )
    
    const response = await deleteServer(server.id)
    if (response.code === 200) {
      ElMessage.success('服务器删除成功')
      refreshServerList()
    } else {
      ElMessage.error(`删除失败: ${response.message}`)
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('删除服务器失败')
      console.error('删除服务器失败:', error)
    }
  }
}


// 保存服务器
const saveServer = async () => {
  try {
    saveLoading.value = true
    
    let response
    if (editingServer.value) {
      // 更新现有服务器
      response = await updateServer(editingServer.value.id, serverForm)
      if (response.code === 200) {
        ElMessage.success('服务器更新成功')
      } else {
        ElMessage.error(`更新失败: ${response.message}`)
        return
      }
    } else {
      // 添加新服务器
      response = await addServer(serverForm)
      if (response.code === 200) {
        ElMessage.success('服务器添加成功')
      } else {
        ElMessage.error(`添加失败: ${response.message}`)
        return
      }
    }
    
    showAddDialog.value = false
    resetForm()
    refreshServerList()
  } catch (error) {
    ElMessage.error('保存失败')
  } finally {
    saveLoading.value = false
  }
}

// 重置表单
const resetForm = () => {
  editingServer.value = null
  Object.assign(serverForm, {
    server_name: '',
    server_ip: '',
    server_port: 80,
    is_maintenance: false,
    remark: ''
  })
}

// 分页处理
const handleSizeChange = (size) => {
  pagination.per_page = size
  refreshServerList()
}

const handleCurrentChange = (page) => {
  pagination.current_page = page
  refreshServerList()
}

// 状态相关方法
const getStatusType = (status) => {
  const statusMap = {
    'online': 'success',
    'offline': 'danger',
    'maintenance': 'warning'
  }
  return statusMap[status] || 'info'
}

const getStatusText = (status) => {
  const statusMap = {
    'online': '在线',
    'offline': '离线',
    'maintenance': '维护中'
  }
  return statusMap[status] || '未知'
}

const getStatusIcon = (status) => {
  const iconMap = {
    'online': 'Check',
    'offline': 'Close',
    'maintenance': 'Warning'
  }
  return iconMap[status] || 'Monitor'
}

const getProgressColor = (percentage) => {
  if (percentage >= 80) return '#f56c6c'
  if (percentage >= 60) return '#e6a23c'
  return '#67c23a'
}

const formatTime = (time) => {
  if (!time) return 'N/A'
  return new Date(time).toLocaleString()
}

// 搜索相关方法
const toggleSearch = () => {
  showSearch.value = !showSearch.value
}

const handleSearch = () => {
  // 实现搜索逻辑
  refreshServerList()
}

const resetSearch = () => {
  Object.assign(searchForm, {
    server_name: '',
    server_ip: '',
    status: '',
    is_maintenance: null
  })
  refreshServerList()
}

// 选择相关方法
const handleSelectionChange = (selection) => {
  selectedRows.value = selection
  hasSelectedRows.value = selection.length > 0
}

const handleBatchEdit = () => {
  if (selectedRows.value.length === 0) {
    ElMessage.warning('请选择要编辑的服务器')
    return
  }
  if (selectedRows.value.length > 1) {
    ElMessage.warning('只能选择一个服务器进行编辑')
    return
  }
  openEditDialog(selectedRows.value[0])
}

const handleBatchDelete = () => {
  if (selectedRows.value.length === 0) {
    ElMessage.warning('请选择要删除的服务器')
    return
  }
  
  // 检查是否所有选中的服务器都是离线状态
  const offlineServers = selectedRows.value.filter(server => server.status === 'offline')
  const onlineServers = selectedRows.value.filter(server => server.status !== 'offline')
  
  if (onlineServers.length > 0) {
    ElMessage.warning('只能删除离线状态的服务器，请取消选择在线服务器')
    return
  }
  
  ElMessageBox.confirm(
    `确定要删除选中的 ${offlineServers.length} 个离线服务器吗？`,
    '确认删除',
    {
      type: 'warning',
      confirmButtonText: '确定删除',
      cancelButtonText: '取消'
    }
  ).then(async () => {
    try {
      // 批量删除离线服务器
      const deletePromises = offlineServers.map(server => deleteServer(server.id))
      await Promise.all(deletePromises)
      
      selectedRows.value = []
      hasSelectedRows.value = false
      ElMessage.success('批量删除成功')
      refreshServerList()
    } catch (error) {
      ElMessage.error('批量删除失败')
      console.error('批量删除失败:', error)
    }
  }).catch(() => {
    // 取消删除
  })
}

// 部署处理方法
const handleDeploy = async () => {
  if (deployForm.serverIds.length === 0) {
    ElMessage.warning('请选择要部署的服务器')
    return
  }
  
  if (!deployForm.description.trim()) {
    ElMessage.warning('请输入部署说明')
    return
  }
  
  try {
    deployLoading.value = true
    
    const response = await deployServers({
      mode: deployForm.mode,
      server_ids: deployForm.serverIds,
      description: deployForm.description
    })
    
    if (response.code === 200) {
      ElMessage.success('部署任务已提交，请查看部署日志')
      showDeployDialog.value = false
      // 重置表单
      deployForm.serverIds = []
      deployForm.description = ''
      deployForm.mode = 'update_restart'
    } else {
      ElMessage.error(`部署失败: ${response.message}`)
    }
  } catch (error) {
    ElMessage.error('部署失败')
    console.error('部署失败:', error)
  } finally {
    deployLoading.value = false
  }
}

// 组件挂载
onMounted(() => {
  refreshServerList()
})
</script>

