import request from '../utils/request'

// 获取服务器列表
export const getServerList = (params: any = {}) => {
  return request({
    url: '/api/v1/test/server-list',
    method: 'get',
    params
  })
}

// 获取服务器详情
export const getServerDetail = (id: number) => {
  return request({
    url: `/api/v1/admin/server/detail/${id}`,
    method: 'get'
  })
}

// 添加服务器
export const addServer = (data: any) => {
  return request({
    url: '/api/v1/admin/server/add',
    method: 'post',
    data
  })
}

// 更新服务器
export const updateServer = (id: number, data: any) => {
  return request({
    url: `/api/v1/admin/server/update/${id}`,
    method: 'post',
    data
  })
}

// 删除服务器
export const deleteServer = (id: number) => {
  return request({
    url: `/api/v1/admin/server/delete/${id}`,
    method: 'delete'
  })
}

// 设置服务器状态
export const setServerStatus = (id: number, status: string) => {
  return request({
    url: `/api/v1/admin/server/set-status/${id}`,
    method: 'post',
    data: { status }
  })
}

// 切换维护状态
export const toggleMaintenance = (id: number, isMaintenance: boolean) => {
  return request({
    url: `/api/v1/admin/server/toggle-maintenance/${id}`,
    method: 'post',
    data: { is_maintenance: isMaintenance }
  })
}

// 批量设置服务器状态
export const batchSetServerStatus = (data: any) => {
  return request({
    url: '/api/v1/admin/server/batch-set-status',
    method: 'post',
    data
  })
}

// 获取服务器状态
export const getServerStatus = () => {
  return request({
    url: '/api/v1/admin/server/status',
    method: 'get'
  })
}

// 获取所有服务器状态
export const getAllServersStatus = () => {
  return request({
    url: '/api/v1/admin/server/all',
    method: 'get'
  })
}

// 部署服务器
export const deployServers = (data: any) => {
  return request({
    url: '/api/v1/admin/server/deploy',
    method: 'post',
    data
  })
}
